export class App {
  router; //This enshures that Typescript doesnt freak out about "this.router" not existing for "App"
  configureRouter(config, router) {
    this.router = router;
    config.title = 'Aurelia';
    config.map([
      { route: ['', 'home'],       name: 'home',       moduleId: 'modules/home/home' }
    ]);
  }
}
